@import "../sass/mixins";
@import "../sass/variables";

.modalWrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  font-family: "Montserrat", sans-serif;
}

.modalBackdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 3;
  background-color: rgba(167, 184, 236, 0.527);
}

.modalBox {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.25);
  z-index: 4;
  animation: opacity 0.8s 
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
